var that;
import mediaItem from 'pages/category/news-item.nvue';
import xwEmpty from '@/components/xw-empty/xw-empty.vue'
import uniSegmentedControl from '@/components/uni-segmented-control/uni-segmented-control.vue';
export default {
	components: {
		mediaItem,
		uniSegmentedControl,
		xwEmpty
	},
	data() {
		return {
			isShow: false,
			tu1: 'https://zzwl1zsxn.oss-cn-hangzhou.aliyuncs.com/images/2020/03/28/33dcae809595b378bb5e42177df02aba.png',
			tu2: 'https://zzwl1zsxn.oss-cn-hangzhou.aliyuncs.com/images/2020/03/28/720bb60bb28f04ac20f6cba3eacae394.png',
			status: 'more',
			page: 1,
			contentText: {
				contentdown: '查看更多',
				contentrefresh: '加载中',
				contentnomore: '没有更多了'
			},
			items: ['周期购', '商品', '套餐'],
			current: 0,
			activeColor: '#65c186',
			styleType: 'text',
			liststate: []
		};
	},
	onLoad() {
		that = this;
		uni.request({
			url: this.$api_url + '/product/collect', //仅为示例，并非真实接口地址。
			method: 'POST',
			header: {
				'Content-Type': 'application/x-www-form-urlencoded',
				'authorization': uni.getStorageSync('authorization') //自定义请求头信息
			},
			data: {
				"type": 0
			},
			success: res => {
				console.log(res)
				if (res.data.code == 1000) {
					that.liststate = res.data.content.productListAppVoList
				} else {
					that.liststate = []
				}
				if (that.liststate.length == 0) {
					that.isShow = true
				} else {
					that.isShow = false
				}
			},
			fail: function(e) {
				that.liststate = []
				that.isShow = false
			}
		});
	},

	methods: {

		onClickItem(index) {
			if (that.current !== index) {
				that.current = index;

			}

			//0fangan 1danpin 2taocan
			uni.request({
				url: this.$api_url + '/product/collect', //仅为示例，并非真实接口地址。
				method: 'POST',
				header: {
					'Content-Type': 'application/x-www-form-urlencoded',
					'authorization': uni.getStorageSync('authorization') //自定义请求头信息
				},
				data: {
					"type": that.current
				},
				success: res => {
					console.log(res)
					if (res.data.code == 1000) {
						that.liststate = res.data.content.productListAppVoList
					} else {
						that.liststate = []
					}
					if (that.liststate.length == 0) {
						that.isShow = true
					} else {
						that.isShow = false
					}
				},
				fail: function(e) {
					that.liststate = []
					that.isShow = false
				}
			});
		},
		goDetail(item) {
			uni.navigateTo({
				url: '/pages/detail/goodsinfo?id=' + item.id +'&type=' + item.type
			});
		},


	}
};
